import React from 'react';
import { useTranslation } from 'react-i18next';

function Home() {
  const { t } = useTranslation();
  return (
    <>
      <h1>{t('pages.home.header')}</h1>
      <p>
        Vítejte na serveru
        {' '}
        <b>var.astro.cz</b>
        , internetovém centru české proměnařiny!
      </p>

      <p>
        <b>Sekce proměnných hvězd a exoplanet České astronomické společnosti</b>
        {' '}
        již od roku 1924 organizuje výzkum a pozorování proměnných hvězd
        v ČR a SR a sdružuje amatérské i profesionální
        výzkumníky na tomto zajímavém poli stelární astrofyziky.
      </p>

      <p>
        Hlavní činnost Sekce spočívá v organizování společného výzkumu
        proměnných hvězd pod 4 výzkumnými projekty:

      </p>

      <ul>
        <li>
          Projekt
          {' '}
          <b>B.R.N.O.</b>
          {' '}
          - pozorování a výzkum zákrytových dvojhvězd
        </li>
        <li>
          Projekt
          {' '}
          <b>MEDÚZA</b>
          {' '}
          - pozorování a výzkum fyzických proměnných hvězd (převážně pulzujících proměnných hvězd)
        </li>
        <li>
          Projekt
          {' '}
          <b>TRESCA</b>
          {' '}
          - sledování tranzitů exoplanet přes mateřskou hvězdu,
          hledání dalších exoplanet u známých tranziterů a hledání nových tranziterů.
        </li>
      </ul>

    </>
  );
}

export default Home;
